    export default {
        data() {
            return {
                code: '',
                mobile: '',
                password: '',
                sending: false,
                sendTime: '获取验证码',
                count: 60,
                logining: false
            }
        },

        methods: {
            sendMsg() {
                const {mobile} = this;
                if (!mobile) {
                    this.$queue.showToast("请输入手机号");
                } else if (mobile.length !== 11) {
                    this.$queue.showToast("请输入正确的手机号");
                } else {
                    this.$queue.showLoading("正在发送验证码...");
                    this.$Request.getT("/user/sendMsg/" + mobile + "/forget").then(res => {
                        if (res.status === 0) {
                            this.sending = true;
                            this.$queue.showToast('验证码发送成功请注意查收');
                            this.countDown();
                            uni.hideLoading();
                        } else {
                            uni.hideLoading();
                            uni.showModal({
                                showCancel: false,
                                title: '短信发送失败',
                                content: res.msg ? res.msg : '请一分钟后再获取验证码',
                            });
                        }
                    });
                }
            },
            countDown() {
                const {count} = this;
                if (count === 1) {
                    this.count = 60;
                    this.sending = false;
                    this.sendTime = '获取验证码'
                } else {
                    this.count = count - 1;
                    this.sending = true;
                    this.sendTime = count - 1 + '秒后重新获取';
                    setTimeout(this.countDown.bind(this), 1000);
                }
            },
            inputChange(e) {
                const key = e.currentTarget.dataset.key;
                this[key] = e.detail.value;
            },
            navBack() {
                uni.navigateBack();
            },


            navTo(url) {
                uni.navigateTo({
                    url
                })
            },
            toLogin() {
                const {mobile, password, code} = this;
                if (!mobile) {
                    this.$queue.showToast("请输入账号");
                } else if (!password) {
                    this.$queue.showToast("请设置密码");
                } else if (password.length < 6) {
                    this.$queue.showToast("密码位数必须大于六位");
                } else {
                    this.logining = true;
                    this.$queue.showLoading("正在修改密码中...");
                    this.$Request.postJson("/user/forgetPwd",{
                        pwd: password,
                        phone: mobile,
                        msg: code
                    }).then(res => {
						uni.hideLoading();
                        if (res.status === 0) {
						
                            uni.navigateTo({
                                url: '/pages/public/login'
                            });
                        } else {
                            
                            uni.showModal({
                                showCancel: false,
                                title: '密码找回失败',
                                content: res.msg,
                            });
                        }
                    });
                }
            },
        },

    }
